.Sidebar {
  background-color: #0B002F;
  height: 480px;
  width: 80px;
  padding: 20px;
  position: fixed;
  display: table-cell;
}

.Sidebar-button {
  height: 50px;
  background-color: #3003C1;
  padding: 10px;
  margin: 3px;
  border-radius: 15px;
}

.Sidebar-button:hover {
  background-color: #f4f4f0;
}



